* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;/* НЕ ЛОМАЕТ АДАПТАЦИЮ*/
    /*flex-direction: column;  ломает адаптацию*/
}

.App {
    width: 800px;
}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;

}

.post__btns {
    display: flex;
}

.remove-btn {
    margin-right: 0.5rem;
}

.post-enter {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
}
.post-enter-active {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: all 500ms ease-in;
}
.post-exit {
    opacity: 1;
}
.post-exit-active {
    -webkit-transform: translateX(-350px);
            transform: translateX(-350px);
    transition: all 500ms ease-in;
}

.page__wrapper {
    margin-top: 10px;
    display: flex;
}

.page {
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;
}

.page__current {
    border: 2px solid orange;
    font-weight: bold;
}

.navbar {
    height: 50px;
    width: 100vh;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: lightgray;
}

.navbar__links {
    margin-left: auto;

}

.link {
    padding: 10px;
    text-decoration: none;
    cursor: pointer;
    font-size: 14pt;
    color: teal;
}
.MyButtton_myBtn__2suVu {
    padding: 5px 15px;
    color: teal;
    font-size: 14px;
    background-color: transparent;
    border: 1px solid teal;
    cursor: pointer;
}
.MyModal_myModal__1pSrQ {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: none;
    background: rgba(0,0,0, .5);
}

.MyModal_myModalContent__3qg6z {
    padding: 25px;
    background: white;
    border-radius: 16px;
    min-width: 250px;
}

.MyModal_myModal__1pSrQ.MyModal_active__38orM {
    display: flex;
    justify-content: center;
    align-items: center;
}
.MyInput_myInput__3JgiO {
    width: 100%;
    padding: 5px 15px;
    margin: 5px 0;
    border: 1px solid teal;
    height: 30px;
}
.Loader_loader__2DSH7 {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 3px dashed teal;
    -webkit-animation: Loader_rotate__1VrEX 1s infinite linear;
            animation: Loader_rotate__1VrEX 1s infinite linear;
}

@-webkit-keyframes Loader_rotate__1VrEX {
    from {
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}

@keyframes Loader_rotate__1VrEX {
    from {
        -webkit-transform: rotate(0deg) scale(1);
                transform: rotate(0deg) scale(1);
    }
    to {
        -webkit-transform: rotate(360deg) scale(1.4);
                transform: rotate(360deg) scale(1.4);
    }
}
